
* {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #111111;
}

.App {
  background-color: #111111;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.headerContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
}

.headerContainer h1 {
  color: white;
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 30px;
  font-family: Arial, Helvetica, sans-serif;
}

.headerContainer input {
  padding: 5px 10px 5px 10px;
  width: 440px;
  height: 50px;
  background-color: #111111;
  border: solid #5c5c5c 2px;
  border-radius: 5px;
  color: white;
  font-weight: 500;
  font-size: 15px;
  transition: ease-in-out 0.2s;
  border-radius: 30px;
}

.buttonContainer {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.buttonContainer img {
  height: 45px;
  margin-left: 30px;
  cursor: pointer;
}

.buttonContainer img:hover {
  transform: scale(1.1);
  transition: all ease-in-out 0.2s;
}

::placeholder {
  color: rgb(255, 255, 255);
  font-weight: 500;
  font-family: Arial, Helvetica, sans-serif;
  padding-left: 10px;
}

.headerContainer input:focus {
  outline: none;
  border: none;
  background-color: #2e2e2e;
  transition: ease-in-out 0.2s;

}

.headerContainer input:hover {
  border: none;
  background-color: #2e2e2e;
  transition: ease-in-out 0.2s;
  .box-shadow-basic {
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.2);
  }
    }

.coinContainer {
  display: flex;
  flex-direction: column;
}

.loadingMssg {
  color: white;
}
