.coinPage-Container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: left;
    margin: 0;
    padding: 20px;
    font-family: Arial, Helvetica, sans-serif;
    height: 100vh;
    width: 100vw;
    gap: 20px;
  }
  
  .coinPage-Info {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 500px;
    height: 600px;
    background-color: rgb(29, 29, 29);
    border-radius: 20px;
  }
  
  .coinPage-Info h1 {
    color: white;
    font-size: 50px;
    text-transform: uppercase;
    font-weight: 700;
    color: #bfbfbf;
    background-color: transparent;
  }
  
  .coinPage-Icon {
    height: 150px;
    margin: 30px 0px 30px 0px;
    background-color: transparent;
  }
  
  .coinPage-Data {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 600px;
    border-radius: 20px;
    background-color: rgb(29, 29, 29);
    gap: 20px;
  }
  
  .coinPage-Row {
    display: flex;
    flex-direction: row;
    width: 65%;
    align-items: center;
    background-color: transparent;
  }
  
  .coinPage-RowData {
    color: rgb(170, 170, 170);
    font-size: 25px;
    font-weight: 400;
    background-color: transparent;
    margin-left: 50px;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .coinPage-RowHeader {
    width: 170px;
    font-size: 40px;
    color: rgb(223, 223, 223);
    font-size: 25px;
    font-weight: 600;
    background-color: transparent;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
  
  .coinPage-RouteButton {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 35px;
    width: 100px;
    background-color: #565656;
    border-radius: 5px;
    color: white;
    font-size: 13px;
    cursor: pointer;
    text-decoration: none;
    text-transform: uppercase;
    font-family: Arial, Helvetica, sans-serif;
  }
  
  .coinPage-RouteButton:active {
    text-decoration: none;
  }
  
  .coinPage-RouteButton:hover {
    transform: scale(1.1);
    transition: all ease-in-out 0.2s;
  }