.coinContainer {
    display: flex;
    justify-content: center;
  }
  
  .coinRow {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 70px;
    padding: 20px;
    background-color: #111111;
  }
.coinRow :hover{
  transform: scale(1.02);
  transition: all ease-in-out 0.1s;
  cursor: pointer;
}
  
  .coinData {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .coin {
    display: flex;
    align-items: center;
    padding-right: 24px;
  }
  
  .coin h1 {
    font-size:17px;
    width: 150px;
    color: white;
    text-align: left;
    margin-left: 30px;
  }
  
  .coin img {
    height: 40px;
    width: 40px;
    margin-right: 10px;
  }
  
  .coinSymbol {
    text-transform: uppercase;
    font-size: 17px;
    color: white;
    text-align: left;
    margin-left: 30px;
    margin-right: 20px;
    width: 50px;
  }
  
  .coinPrice {
    font-size: 17px;
    color: white;
    text-align: left;
    margin-left: 30px;
    text-align: left;
    width: 100px;
  }
  .coinVolume {
    font-size: 17px;
    color: white;
    text-align: left;
    margin-left: 30px;
    text-align: left;
    white-space: nowrap;
  }
  
  .coinData {
    display: flex;
    text-align: right;
    justify-content: space-between;
    width: 100%;
  }
  
  .coinPrice {
    width: 110px;
  }
  
  .coinVolume {
    width: 155px;
  }
  
  .priceChange {
    width: 90px;
    font-size: 17px;
    text-align: right;
  }
  
  .red {
    color: #f00606;
  }
  
  .green {
    color: #11d811;
  }
  
  .coin button {
    margin-left: 30px;
    height: 50px;
    border: none;
    border-radius: 50%;
    color: rgb(90, 90, 90);
    font-weight: 500;
    font-size: 26px;
    cursor: pointer;
  }
  
  